  const handleSave = (loadGrid, idPrefix, doClose) => {

    if(idPrefix === undefined) idPrefix = "";

    var json = {};
      
    if(document.getElementById("logging_tracing_component_id" + idPrefix).value.endsWith(":") !== true) {
      json["logging_tracing_component"] = document.getElementById("logging_tracing_component_id" + idPrefix).value;
    }
    if(document.getElementById("logging_tracing_file_name_id" + idPrefix).value.endsWith(":") !== true) {
      json["logging_tracing_file_name"] = document.getElementById("logging_tracing_file_name_id" + idPrefix).value;
    }
    json["logging_tracing_level"] = document.getElementById("logging_tracing_level_id" + idPrefix).value;
    json["logging_tracing_max_file_size"] = document.getElementById("logging_tracing_max_file_size_id" + idPrefix).value;
    json["logging_tracing_max_rollover_files"] = document.getElementById("logging_tracing_max_rollover_files_id" + idPrefix).value;
    json["logging_tracing_compress"] = document.getElementById("logging_tracing_compress_id" + idPrefix).checked.toString();
      
    loadGrid( json["logging_tracing_component"], json["logging_tracing_file_name"], json["logging_tracing_level"], json["logging_tracing_max_file_size"], json["logging_tracing_max_rollover_files"], json["logging_tracing_compress"], true); 

    handleClose(idPrefix, doClose);
  };

  const handleAdd = (idPrefix, doAdd, doClose) => {
    if(idPrefix === undefined) idPrefix = "";


    document.getElementById("logging_tracing_component_id").value = "";
    document.getElementById("logging_tracing_file_name_id").value = "";
    document.getElementById("logging_tracing_level_id").value = "";
    document.getElementById("logging_tracing_max_file_size_id").value = "";
    document.getElementById("logging_tracing_max_rollover_files_id").value = "";
    document.getElementById("logging_tracing_compress_id").checked = false;

    if(doAdd) {
      doAdd();
    }

    handleClose(idPrefix,  doClose);
  };

  const handleClose = (idPrefix, doClose) => {
    updateStyle("logging_div" + idPrefix, "display", "block");
    document.getElementById("addlogging_tracingPage" + idPrefix).style.display = "none";

      if(doClose) {
        doClose();
      }
  };

  const closeAdd = (idPrefix) => {
      if(idPrefix === undefined) idPrefix = "";

      updateStyle("logging_tracing_div" + idPrefix, "display", "block");
      document.getElementById("logging_tracing_tab" + idPrefix).style.display = "flex";

  };


